$base: 0.6rem;

.scroll {
 position: absolute;
 bottom: 10em;
 display: flex;
 justify-content: center;
 align-items: center;
}

.chevron {
  position: absolute;
  width: $base * 3.5;
  height: $base * 0.8;
  opacity: 0;
  transform: scale(0.3);
  animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
  animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move-chevron 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
 content: '';
 position: absolute;
 top: 0;
 height: 100%;
 width: 50%;
 background: #c1c1c2;
 box-shadow: 0px 0px 15px 1px white;
}

.chevron:before {
 left: 0;
 transform: skewY(30deg);
}

.chevron:after {
 right: 0;
 width: 50%;
 transform: skewY(-30deg);
}

@keyframes move-chevron {
 25% {
  opacity: 1;
	}
 33.3% {
  opacity: 1;
  transform: translateY($base * 3.8);
 }
 66.6% {
  opacity: 1;
  transform: translateY($base * 5.2);
 }
 100% {
  opacity: 0;
  transform: translateY($base * 8) scale(0.5);
 }
}

@keyframes timeline {
0%{
  box-shadow: inset 0px 0px 0px 0px white;
  opacity: 0;
}
50%{
  box-shadow: inset 0px 0px 15px 0px white;
  opacity: 1;
}
100%{
  box-shadow: inset 0px 0px 0px 0px white;
  opacity: 0;
}

}


